@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css);
h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 64px;
}

.center {
  text-align: center;
}

.fab,
.fas,
.far {
  color: #292929;
  font-size: 28px;
  margin-left: 0.6em;
  margin-right: 0.6em;
}

.demo_resume {
  margin-top: 6em;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  padding: 2em;
  width: 60%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  box-shadow: 6px 12px 120px rgba(128, 128, 128, 0.329);
}

#social-icons, #demo-name, #demo-title {
  text-align: center;
}

#social-icons {
  margin-bottom: 2em;
}

#demo-resume-header {
  border-bottom: #292929 solid;
  border-width: 1px;
  background-color: white;
  width: 100%;
}

#features-section {
  margin-top: 12em;
  display: -webkit-flex;
  display: flex;
}

.demo-card {
  text-align: center;
  border: none;
  padding: 4em;
  border-radius: 6px;
  text-align: center;
  box-shadow: 3px 9px 120px rgba(128, 128, 128, 0.329);
  overflow: hidden;
  box-sizing: border-box;
}

#demo-card-title {
  margin-top: 4em;
}

#inner-feature-section {
  margin-right: 4em;
}

.notification {
  padding: 2em;
  background-color: white;
  overflow: hidden;
  box-shadow: 6px 12px 120px rgba(128, 128, 128, 0.329);
  width: 56%;
  margin-top: 10em;
}

@media screen and (max-width: 1300px) {
  #features-section {
    display: none;
    padding: 0;
    width: 40%;
  }
  .notification {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  h1 {
    font-size: 42px;
  }
  .notification button {
    margin-left: auto;
    margin-right: auto;
  }
}


.Header {
  padding: 2.4em;
}

.logo {
  float: left;
  text-decoration: none;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  color: #292929;
}

.header_options {
  float: right;
}

.option {
  color: #292929;
  font-family: "Roboto", sans-serif;
  font-weight: inherit;
  text-decoration: none;
  margin-right: 2em;
}

.option:hover {
  color: #0085FF;
}
