.Footer {
  padding: 3em;
  background-color: #0000b4;
  margin-top: 8em;
}

.light-text {
  color: white;
}

ul {
  margin: 0;
  padding: 0px;
}

ul li {
  list-style: none;
  line-height: 24px;
}

.footer-option {
  color: rgba(255, 255, 255, 0.623);
  cursor: pointer;
  text-decoration: none;
}

.footer-option:hover {
  transition: 0.1s ease-in-out;
  color: white;
}

.Footer-upper-section {
  display: -webkit-flex;
  display: flex;
}

#footer-option-list {
  margin-right: 5em;
}

@media screen and (max-width: 1000px) {
  .Footer-upper-section {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  #footer-option-list {
    text-align: center;
  }
}
