.LoginActivity {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

#form-container {
  margin-top: 4em;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  box-shadow: 3px 9px 120px rgba(128, 128, 128, 0.329);
  padding: 3em;
  width: 24%;
  border-radius: 16px;
  -webkit-align-content: content;
          align-content: content;
}

.form-input-label {
  font-weight: bold;
}

.form-input {
  padding: 1em;
  border: #0000c8 solid;
  border-radius: 4px;
  border-width: 2.2px;
  width: 90%;
  margin-right: auto;
  margin-left: auto;
  margin-top: 1em;
  margin-bottom: 2em;
}

.form-input:focus {
  outline: none;
  background-color: rgba(0, 0, 200, 0.07);
}

.form-input::-webkit-input-placeholder {
  font-size: 15px;
  font-family: inherit;
}

.form-input:-ms-input-placeholder {
  font-size: 15px;
  font-family: inherit;
}

.form-input::placeholder {
  font-size: 15px;
  font-family: inherit;
}

button:focus, input[type=reset]:focus, input[type=submit]:focus {
  outline: none;
}
